.hotspot-list-item {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items:center;
	border: 1px solid #e5e5e5;
	border-bottom: none;
	p {
		margin: 0;
		padding: 6px;
		font-size: 12px;
	}

	&:last-of-type{
		border-bottom: 1px solid #e5e5e5;
	}

}

.hotspots-editor-bottomtip {
	position: absolute;
	bottom: 14px;
	left: 50%;
	transform: translateX(-50%);
	padding: 2px 8px;
	z-index: 10;
	font-size: 14px;
	line-height: 1.4;
	color: #333;
	background: rgba(255,255,255,0.7);
	border-radius: 4px;
	visibility: hidden;
	&.visible {
		visibility: visible;
	}
}

.components-panel__body.hotspot-settings { 
	
	border-radius: 4px;
	background: #f5f5f5;
	flex: 1;

	> .components-panel__body-title button {
		border-radius: 4px;
		font-weight: normal;
		font-size: 12px;
		padding: 10px 42px 10px 10px;

		> span svg {
			height: 18px;
		}
	}
	> div {
		padding: 0px;
	}
}